import React from 'react';

import { TourStepButton } from '../../../api/src/domain/TourStepButton';
import { Popup } from './Popup';

export type TourStepPopupProps = {
  active: boolean;
  buttons: TourStepButton[];
  html: string;
  onAction: (action: TourStepButton['action']) => void;
  onOut?: () => void;
  targetEl?: HTMLElement;
};

export const TourStepPopup: React.FC<TourStepPopupProps> = ({
  active,
  buttons,
  html,
  onAction,
  onOut = () => undefined,
  targetEl,
}) => {
  return (
    <Popup
      anchorEl={targetEl}
      buttons={buttons.map((b) => [
        <button
          className={`${b.action === 'next' ? 'enactive-primary' : ''}`}
          key={b.action}
          onClick={() => onAction(b.action)}
        >
          {b.text}
        </button>,
      ])}
      open={active}
      onOutEnd={onOut}
    >
      <div dangerouslySetInnerHTML={{ __html: html }} />
    </Popup>
  );
};
