import React from 'react';

import { useStaticInterval } from './useStaticInterval';

export type UseIntervalValueOpts = {
  interval?: number;
};

export function useIntervalValue<V extends string | number | boolean>(
  f: () => V,
  opts?: UseIntervalValueOpts,
): V | undefined;
export function useIntervalValue<V extends string | number | boolean>(
  f: () => V,
  initialValue?: V,
  opts?: UseIntervalValueOpts,
): V;
export function useIntervalValue<V extends string | number | boolean>(
  f: () => V,
  initialValueOrOpts?: V | UseIntervalValueOpts,
  opts?: UseIntervalValueOpts | undefined,
): V | undefined {
  const [value, setValue] = React.useState(() => {
    if (initialValueOrOpts !== undefined) {
      switch (typeof initialValueOrOpts) {
        case 'string':
        case 'number':
        case 'boolean':
          return initialValueOrOpts;
        default:
          return undefined;
      }
    } else {
      return undefined;
    }
  });

  useStaticInterval(() => setValue(f()), {
    interval:
      typeof initialValueOrOpts === 'object'
        ? initialValueOrOpts.interval
        : typeof opts === 'object'
        ? opts.interval
        : undefined,
  });

  return value;
}
