/* eslint-disable no-console */

export function attributesToObject(
  entries: IterableIterator<[string, string]>,
): Record<string, unknown> {
  const result: Record<string, unknown> = {};
  for (const [key, value] of entries) {
    const [init, last] = parseFlattenedKeys(key);
    let level = result;
    for (const part of init) {
      if (part in level && typeof level[part] !== 'object') {
        console.warn(
          'Assigned both a value and an object on enactive script tag metadata',
          { key, value: level[part] },
        );
      }

      if (part in level) {
        level = level[part] as Record<string, unknown>;
      } else {
        level = level[part] = {};
      }
    }

    if (last in level) {
      console.warn(
        'Assigned duplicate values on enactive script tag metadata',
        { key, value },
      );
    }
    level[last] = value;
  }
  return result;
}

function parseFlattenedKeys(key: string): [string[], string] {
  const init = splitDot(key).map(sausageToCamel);
  const last = init.pop() ?? '';
  return [init, last];
}

function splitDot(str: string): string[] {
  return (
    str
      // Trim dots
      .replaceAll(/^\.+|\.+$/g, '')
      // Reduce multiple dots
      .replaceAll(/\.+/g, '.')
      .split('.')
  );
}

function sausageToCamel(str: string): string {
  // Convert sausage-case-keys to camelCaseKeys
  return (
    str
      // Trim dashes
      .replaceAll(/^-+|-+$/g, '')
      // De-duplicate successive dashes
      .replaceAll(/-+/g, '-')
      .split('-')
      .map((w, i) =>
        i > 0 && w.length > 0
          ? `${w[0].toUpperCase()}${w.slice(1).toLowerCase()}`
          : w.toLowerCase(),
      )
      .join('')
  );
}
