import { useIntervalValue } from './useIntervalValue';

const global = window;

export type UseLocationHrefProps = {
  interval?: number;
  window?: Window;
};

export const useLocationHref = ({
  interval = 250,
  window = global,
}: UseLocationHrefProps = {}): string => {
  const href = useIntervalValue(
    () => window.location.href,
    window.location.href,
    { interval },
  );
  return href;
};
