import { styled } from '@material-ui/styles';
import React, { CSSProperties } from 'react';
import { CSSTransition } from 'react-transition-group';

export type MaskProps = {
  active: boolean;
  onOutEnd?: () => void;
  ref?: React.RefCallback<HTMLElement>;
  style?: CSSProperties;
};

const MaskInner = styled('div')({
  '&, &.enter, &.exit.exit-active, &.exit-done': {
    opacity: 0,
    pointerEvents: 'none',
  },
  '&.enter-active, &.exit-active': {
    transition: 'opacity 250ms linear',
  },
  '&.enter.enter-active, &.enter-done, &.exit': {
    opacity: 1,
    pointerEvents: 'auto',
  },
  borderColor: 'rgba(0, 0, 0, 0.7)',
  borderStyle: 'solid',
  bottom: 0,
  left: 0,
  position: 'fixed',
  right: 0,
  top: 0,
  zIndex: 1111,
});

export const Mask: React.FC<MaskProps> = ({
  active,
  children,
  onOutEnd = () => undefined,
  ref,
  style,
}) => {
  const maskRef = React.useRef<HTMLElement | null>(null);

  return (
    <CSSTransition
      in={active}
      onExited={onOutEnd}
      nodeRef={maskRef}
      timeout={250}
    >
      <MaskInner
        ref={(el: HTMLElement | null) => {
          maskRef.current = el;
          ref?.(el);
        }}
        style={style}
      >
        {children}
      </MaskInner>
    </CSSTransition>
  );
};
