import React from 'react';

export function useStaticInterval(
  f: () => void,
  { interval = 250 }: { interval?: number } = {},
): void {
  React.useEffect(() => {
    const handler = setInterval(f, interval);
    return () => clearInterval(handler);
    // We do not add `f` in there because it is not expected to change (i.e., "static"). This makes
    // it a bit easier for the usual case because then you don't have to create a useCallback
    // first then apply it to the interval. But be careful, you shouldn't be using this and then
    // referencing react state values. The use-case for this is extremely limited as a result.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [interval]);
}
