/**
 * Temporary replacement for `??=` which doesn't work in CRA right now.
 * https://github.com/facebook/create-react-app/issues/10734
 */
export function nullishAssignment<T extends Record<string, unknown>>(
  obj: T,
  key: keyof T,
  value: T[typeof key],
): void {
  if (obj[key] === undefined || obj[key] === null) {
    obj[key] = value;
  }
}
